<template>
  <div>
    <van-cell title="" :label="wallet.address" :value="wallet.name"></van-cell>
    <van-cell-group title="">
      <van-field placeholder="" label="ATOM" type="number" />
      <van-field placeholder="" label="" rows="1" autosize type="textarea">
        <van-icon name="scan" size="20" slot="button"></van-icon>
      </van-field>
      <van-field placeholder="" label="" />
      <van-field placeholder="" label="" value="0.6 ATOM" disabled />
    </van-cell-group>
    <div class="m-20">
      <van-button color="#333" block></van-button>
    </div>
  </div>
</template>

<script>
import baseMixin from "../../store/baseMixin";

export default {
  mixins: [baseMixin],
  data() {
    return {
      wallet: {
        name: "",
        address: ""
      }
    };
  },
  created() {
    const { address } = this.$route.query;
    this.wallet = this.walletList[address];
  },
};
</script>